import Data from "./Component/compot";
import "./styles.css";

export default function App() {
  const Icon = ({ title, left }) => (
    <div style={{ display: "flex" }}>
      {left}
      <div>{title}</div>
    </div>
  );

  const Box = ({ children }) => {
    return <div>{children}</div>;
  };

  const Row = ({ children, gap = 0, p = 0, border = 0 }) => {
    return (
      <div
        style={{
          display: "flex",
          gap,
          padding: p,
          border: `${border}px solid black`
        }}
      >
        {children}
      </div>
    );
  };

  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>
      <Icon title={"Hello world!"} left={<>@</>} />
      <Box>
        <Icon title={"Hello"} />
        <Icon title={"Hello"} />
        <Icon title={"Hello"} />
        <Icon title={"Hello"} />
      </Box>
      <Row p={10} gap={12} border={2}>
        <div>Hello</div>
        <div style={{ color: "red" }}>Hello</div>
        <div>Hello</div>
        <div>Hello</div>
      </Row>
      <Data />
    </div>
  );
}
